import React, { useState, useEffect } from 'react';
import "./styles.scss";
import { Link, withRouter } from "react-router-dom";

const NavBar = (props) => {
    const [headerclass, setHeaderclass] = useState("")
    const updateNavHeader = () => {
        setHeaderclass(window.scrollY === 0 ? 'at-top':'')
    }
    useEffect(() => {
        if (props.location.hash === '#portfolio'){
            try {
                window.scroll({
                    top: 0.9 * window.innerHeight,
                    left: 0,
                    behavior: 'smooth',
                });
                } catch (error) {
                window.scrollTo(0, 0);
                }
        } else{
            try {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth',
                });
                } catch (error) {
                window.scrollTo(0, 0);
                }
        }
        window.addEventListener("scroll", updateNavHeader)
        return () => {
            window.removeEventListener("scroll", updateNavHeader)
        }
    }, [props.location.hash])
    return (
        <React.Fragment>
        <nav className={`navbar ${headerclass}`}>
            <div className="links">
                <Link className="nav-link" to="/">home</Link>
                <Link className="nav-link" to="/#portfolio">Portfolio</Link>
            </div>
        </nav>
        <div className="navbar-filler"></div>
        </React.Fragment>
    )
}

export default withRouter(NavBar);